export default class AppView {
  constructor(model) {
    this.model = model;
    AppView.renderBase();
  }

  static getSBox() {
    return document.getElementById('sbox');
  }

  static getSearchValue() {
    return AppView.getSBox().value;
  }

  static getUl() {
    return document.getElementById('idlist');
  }

  static renderBase() {
    const divid = 'maindiv';
    const divel = document.getElementById(divid);
    if (!divel) {
      const content = document.createElement('div');
      content.id = divid;
      content.className = 'input-wrap';
      content.innerHTML = '<div class="img"></div> <input id="sbox" class="input" type="text" name="search" placeholder="What kind of video  do you want to look for ? ">';
      document.body.appendChild(content);
      const wrapper = document.createElement('div');
      wrapper.id = 'wrapper';
      wrapper.className = 'clip-wrapper';
      document.body.appendChild(wrapper);
      const ul = document.createElement('ul');
      ul.id = 'idlist';
      ul.className = 'clip-list';
      wrapper.appendChild(ul);
    }
  }

  static removeAllChildren(el) {
    let child = el.lastElementChild;
    while (child) {
      el.removeChild(child);
      child = el.lastElementChild;
    }
  }

  async render(str) {
    let searchStr = AppView.getSearchValue();
    if (str !== undefined) {
      searchStr = str;
    }
    if (searchStr === '') {
      return;
    }

    const items = await this.model.getClips(searchStr);
    AppView.removeAllChildren(AppView.getUl());

    items.forEach((item) => {
      AppView.getUl().appendChild(AppView.createClip(item));
    });
  }

  static createClip(item) {
    const clip = document.createElement('li');
    clip.className = 'clip';

    const divClip = document.createElement('div');
    clip.appendChild(divClip);

    divClip.className = 'clip-div';
    divClip.innerHTML = `<img class="clip-pic" src="${item.snippet.thumbnails.medium.url}" alt="${item.snippet.title}">`;

    const divTitle = document.createElement('div');
    divTitle.className = 'clip-title';
    divClip.appendChild(divTitle);
    divTitle.innerHTML = `<a class="clip-link" href="https://www.youtube.com/watch?v=${item.id.videoId}" target="_blank">${item.snippet.title}</a>`;

    const divClipInfo = document.createElement('div');
    divClip.appendChild(divClipInfo);
    divClipInfo.className = 'clip-info';

    const divChannelTitle = document.createElement('p');
    divChannelTitle.className = 'clip-channel';
    divChannelTitle.innerText = item.snippet.channelTitle;
    divClipInfo.appendChild(divChannelTitle);

    const divDate = document.createElement('p');
    divDate.className = 'clip-date';
    const date = item.snippet.publishedAt.substring(0, 10);
    divDate.innerText = date;
    divClipInfo.appendChild(divDate);

    const divCount = document.createElement('p');
    divCount.className = 'clip-view-count';
    divCount.innerText = item.statistics.viewCount;
    divClipInfo.appendChild(divCount);

    const divDescription = document.createElement('p');
    divDescription.className = 'clip-description';
    divDescription.innerText = item.snippet.description;
    divClipInfo.appendChild(divDescription);

    return clip;
  }
}
