export default class AppModel {
  constructor() {
    this.state = {
      base: 'https://www.googleapis.com',
      search: '/youtube/v3/search',
      video: '/youtube/v3/videos',
      key: 'AIzaSyCRjSQT34clRBjviHg5VIKPamDvygPgHXg',
      maxResults: 15,
    };
  }

  async getClips(search) {
    const urlS = new URL(this.state.search, this.state.base);
    const params = new URLSearchParams(urlS.search);
    params.append('key', this.state.key);
    params.append('type', 'video');
    params.append('part', 'snippet');
    params.append('maxResults', this.state.maxResults);
    params.append('q', search);

    urlS.search = params.toString();

    const responce = await fetch(urlS);
    const data = await responce.json();
    this.search = data;
    this.ids = data.items.map(item => item.id.videoId);
    this.videos = await this.getVideos(this.ids);

    const items = this.search.items.map((item) => {
      const s = this.videos.items.find(f => f.id === item.id.videoId);
      if (s !== undefined) {
        const upd = item;
        upd.statistics = s.statistics;
        return upd;
      }
      return item;
    });
    this.items = items;
    return items;
  }

  async getVideos(ids) {
    const urlV = new URL(this.state.video, this.state.base);
    const params = new URLSearchParams(urlV.search);
    params.append('key', this.state.key);
    params.append('part', 'snippet,statistics');
    params.append('id', ids.join(','));

    urlV.search = params.toString();

    const responce = await fetch(urlV);
    const data = await responce.json();

    return data;
  }
}
